<template>
  <div class="dashboard container-fluid no-gutters">
    <div class="row shadow-sm sticky-top sticky-auto">
      <h5 class="col title-item">性能信息</h5>
    </div>
    <div class="row mt-2">
      <div id="cpu" class="col-md-6"></div>
      <div id="mem" class="col-md-6"></div>
    </div>
    <div class="row shadow-sm sticky-top sticky-auto">
      <h5 class="col title-item">端口信息</h5>
    </div>
    <div class="row net-table" v-if="portForm.length">
      <table class="table">
        <thead class="thead-light">
          <tr>
            <th scope="col">协议</th>
            <th scope="col">地址</th>
            <th scope="col">状态</th>
            <th scope="col">pid/进程</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in portForm" :key="index">
            <th scope="row">{{ item[0] }}</th>
            <td>{{ item[1] }}</td>
            <td>{{ item[3] }}</td>
            <td>{{ item[4] }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else class="row justify-content-center p-5">
      <div class="text-center">
        <div class="spinner-border" role="status">
          <span class="sr-only">Loading...</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import draw from "../utils-admin/mixindraw";
export default {
  data() {
    return {
      cpu: 0,
      MenPercent: 0,
      portForm: [],
      timer: {
        cpu: null,
        mem: null
      }
    };
  },
  mixins: [draw],
  methods: {
    createCpu() {
      let data = [];
      let max = Date.now() + 18000;
      let min = Date.now() + 1000;
      var option = {
        title: {
          text: "cpu % 利用率"
        },
        tooltip: {
          show: true,
          trigger: "axis"
        },
        xAxis: {
          type: "time",
          interval: 6000,
          max: max,
          min,
          splitLine: {
            show: true
          }
        },
        yAxis: {
          type: "value",
          max: 100,
          splitLine: {
            show: true
          }
        },
        series: [
          {
            name: "% ",
            type: "line",
            showSymbol: false,
            hoverAnimation: false,
            areaStyle: {},
            data
          }
        ]
      };
      let xtimes = 0;
      let date = Date.now();
      // 使用刚指定的配置项和数据显示图表。
      return setInterval(() => {
        xtimes++;
        if (xtimes > 18) {
          option.xAxis.max = option.xAxis.max + 1000;
          option.xAxis.min = option.xAxis.min + 1000;
          // data.splice(0, 1);
        }
        data.push([Date.now(), this.cpu]);
        this.MyDraw(option, "cpu");
      }, 1000);
    },
    createMem() {
      let totalData = [];
      let useData = [];
      let max = Date.now() + 18000;
      let min = Date.now() + 1000;
      let ymax = null;
      var option = {
        title: {
          text: "进程内存 MB 使用率"
        },
        tooltip: {
          show: true,
          trigger: "axis"
        },
        xAxis: {
          type: "time",
          interval: 6000,
          max: max,
          min,
          splitLine: {
            show: true
          }
        },
        yAxis: [
          {
            type: "value",
            max: ymax,
            splitLine: {
              show: true
            }
          },
          {
            type: "value",
            max: ymax,
            splitLine: {
              show: true
            }
          }
        ],
        series: [
          {
            name: "MB 总大小",
            type: "line",
            showSymbol: false,
            hoverAnimation: false,
            data: totalData
          },
          {
            name: "MB 已使用",
            type: "line",
            showSymbol: false,
            areaStyle: {},
            hoverAnimation: false,
            data: useData
          }
        ]
      };
      let xtimes = 0;
      let date = Date.now();
      // 使用刚指定的配置项和数据显示图表。
      return setInterval(() => {
        xtimes++;
        if (xtimes > 18) {
          option.xAxis.max = option.xAxis.max + 1000;
          option.xAxis.min = option.xAxis.min + 1000;
        }
        option.yAxis[0].max = Math.ceil(this.MenPercent[0]);
        option.yAxis[1].max = Math.ceil(this.MenPercent[0]);
        totalData.push([Date.now(), this.MenPercent[0]]);
        useData.push([Date.now(), this.MenPercent[1]]);
        this.MyDraw(option, "mem");
      }, 1000);
    },
    addListenSocket() {
      if (!this.$socket.id) {
        let timeOut = setTimeout(() => {
          this.addListenSocket();
          clearTimeout(timeOut);
        }, 500);
        return;
      }
      this.$socket.emit("cpu", { id: this.$socket.id });
      // 获取port信息
      this.$socket.on(this.$socket.id + "_nets", data => {
        this.portForm = data;
        this.$socket.removeListener(this.$socket.id + "_nets");
      });
      this.$socket.emit("nets", { id: this.$socket.id + "_nets" });
      //end
      this.$socket.on(this.$socket.id + "_cpu", msg => {
        this.cpu = msg.CpuPercent;
        this.MenPercent = msg.MenPercent;
      });
    },
    addListenEchar() {
      this.timer.cpu = this.createCpu();
      this.timer.mem = this.createMem();
    },
    removeListent() {
      this.$socket.emit(this.$socket.id + "_cpu_remove", {
        id: this.$socket.id + "_cpu"
      });
      this.$socket.removeListener(this.$socket.id + "_cpu");
      clearInterval(this.timer.cpu);
      clearInterval(this.timer.mem);
    }
  },
  activated() {
    this.showLoading(["#cpu", "#mem"]);
    this.addListenSocket();
    this.addListenEchar();
  },
  deactivated() {
    this.removeListent();
  }
};
</script>

<style lang="scss" scoped>
.dashboard {
  height: calc(100vh - 56px);
  overflow: auto;
  ::-webkit-scrollbar {
    display: none;
  }
}
#cpu,
#mem {
  min-height: 250px;
  min-width: 250px;
}
.net-table {
  overflow: hidden;
  overflow-x: auto;
}
.sticky-auto {
  height: 50px;
  font-weight: bold;
  font-size: 16px;
  .title-item {
    line-height: 50px;
    background-color: #fff;
    font-weight: bold;
  }
}
.emty {
  box-sizing: border-box;
  padding: 50px;
  font-size: 60px;
  display: flex;
  justify-content: center;
  align-content: center;
}
</style>